.link {
    text-decoration: none;
    color: greenyellow;
    margin: 15px;
    padding-bottom: 15px;
}

.link:hover {
    color: gold;
}


.header {
    display: flex;
    width: 250px;

    transition-duration: 300ms;
    transform: translate(-280px);
}

.header:hover {
    transition-duration: 500ms;
    transform: translate(0);
}
