.input:focus {
    outline: none;
    border: greenyellow;
}


.superInput {
    border: 0;
    border-bottom: 1px solid #eee;
}

.errorInput {

    background-color: #fafafa;
    border: 0;
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.3);
    transition: .5s box-shadow;
    padding: 5px;
    border-radius: 10px;
    margin: 5px;
}

.errorInput:hover {
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.5);
}

.error {
    color: red;
}

